<template lang="pug">
  .ocorrencias
    .ocorrencia(v-for='ocorrencia in ocorrencias')
      .dados
        .data
          span {{ ocorrencia.data }}
        .titulo
          h4 {{ ocorrencia.tipo }}
          span(:class='ocorrencia.status') {{ ocorrencia.andamento}}
        .endereco
          p {{ ocorrencia.endereco }} | {{ ocorrencia.turno }} às {{ ocorrencia.horario }}
      .acoes
        select
          option Ação 1
          option Ação 2
          option Ação 3
        button(@click="atualizarStatus(ocorrencia)").verde Marcar como resolvida
</template>

<script>
import AtualizarOcorrenciaService from '../../scripts/services/AtualizarOcorrenciaService'

import { EventBus } from '../../scripts/utils/EventBus'

export default {
  props: ['ocorrencias'],
  methods: {
    atualizarStatus(ocorrencia) {
      AtualizarOcorrenciaService.atualizarOcorrencia(ocorrencia.id)
        .then(response => {
          EventBus.$emit('status-resolvida')
        })  
    }
  }
}
</script>


<style lang="scss">
.ocorrencias {
  .ocorrencia {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    border-bottom: 1px solid $cor-cinza-claro;
    padding-bottom: 30px;
    margin-bottom: 30px;

    .dados {
      .data {
        margin-bottom: 5px;
        font-size: 14px;
        color: $cor-azul-claro;
      }

      .titulo {
        margin-bottom: 20px;
        display: flex;
        align-items: center;

        h4 {
          margin-right: 20px;
        }

        span {
          font-size: 14px;
          color: #fff;
          padding: 2px 10px;
          border-radius: 3px;

          &.verde {
            background: $cor-verde-claro;
          }

          &.vermelho {
            background: $cor-vermelho-claro;
          }

          &.laranja {
            background: $cor-laranja;
          }
        }
      }

      .endereco {
        p {
          font-size: 14px;
        }
      }
    }

    .acoes {
      select {
        width: 136px;
        border-radius: 5px;
      }

      button {
        font-size: 16px;
        height: 40px;
        padding: 0 20px;
        border-radius: 5px;
        margin-left: 20px;
      }
    }
  }
}
</style>




