<template lang="pug">
  .ocorrencias-pesquisa(v-if='ocorrencias.length')
    .ocorrencia(v-for="ocorrencia in ocorrencias")
      .header
        h3 {{ocorrencia.tipo}}
        span {{ocorrencia.data}}
      .content
        p(v-if="ocorrencia.detalhes") {{ocorrencia.detalhes}}
        p(v-else) Ocorrência sem mais detalhes
  .ocorrencias-pesquisa(v-else)
    h3 Nenhuma ocorrência cadastrada
</template>

<script>
import BuscaOcorrenciaService from '../../scripts/services/BuscaOcorrenciaService'

export default {
  data() {
    return {
      ocorrencias: []
    }
  },
  methods: {
    buscarTodasOcorrencias() {
      BuscaOcorrenciaService.buscarOcorrencias()
        .then(response => {
          for(var ocorrencia in response.data) {
            this.ocorrencias.push(response.data[ocorrencia])
          }
          this.ocorrencias = this.ocorrencias.reverse().slice(0,2)
        })
    }
  },
  mounted() {
    this.buscarTodasOcorrencias()
  }
}
</script>

<style lang="scss">
  .ocorrencias-pesquisa {
    padding: 10px 15px;
    .ocorrencia {
      border-bottom: 1px solid $cor-cinza-claro;
      padding: 10px 0;

      &:last-child {
        border: none;
      }

      .header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 10px;

        h3 {
          flex: 1;
          font-size: 16px;
        }

        span {
          font-size: 14px;
          opacity: 0.5;
        }
      }
    }
  }
</style>




